import Vue from 'vue';
var Tree=require('../../../lib/areaTree.js');
//var a=require('../../../lib/areaTree.js')
console.log(Tree)
console.log(Tree['ct-adc-areaTree']);
//import AreaTree from '../../../src/js/areaTree/areaTree.vue';
new Vue({
    el:'#app',
    components:{
        //'area-tree':AreaTree
        'area-tree':Tree['ct-adc-areaTree']
    },
    data:{
        areaToAddAuto:'',
        areaToAddManually:'',
        resultWithId:'',
        resultWithName:'',
        sep:'_'
    },
    methods:{
        getChecked(){
            this.resultWithId=JSON.stringify(this.$refs.areaTree.getChecked(false));
            this.resultWithName=JSON.stringify(this.$refs.areaTree.getChecked(true));
        },
        setChecked(){
            this.$refs.areaTree.setChecked(this.areaToAddManually);
        },
        change(data){
            this.resultWithName=JSON.stringify(data);
        }
    }
});


